@mixin bp($point) {
   @if $point == bp-xsmaller {
     @media (min-width: 27em) { @content ; }
  }
   @else if $point == bp-xsmall {
     @media (min-width: 40em) { @content ; }
  }
    @else if $point == bp-small {
     @media (min-width: 45em) { @content ; }
  }
   @else if $point == bp-medium {
     @media (min-width: 50em)  { @content ; }
  }
  @else if $point == bp-large {
     @media (min-width: 62em)  { @content ; }
  }
  @else if $point == bp-xlarge {
     @media (min-width: 70em)  { @content ; }
  }
  @else if $point == bp-lightbox {
     @media (max-width: 55em)  { @content ; }
  }
}

@mixin flexbox-container {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}

@mixin flex($values) {
  -webkit-box-flex: $values;
  -moz-box-flex: $values;
  -webkit-flex: $values;
  -ms-flex: $values;
  flex: $values;
}

@mixin flexbox-order($ordernumber) {
  order: $ordernumber;
  -webkit-box-ordinal-group: $ordernumber;  
  -moz-box-ordinal-group: $ordernumber;
  -webkit-order: $ordernumber;
  -ms-flex-order: $ordernumber;
}

@mixin slidebar-arrow-orange {
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,)
}

@mixin borderbox { 
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}